import React, { useEffect, useState } from 'react';
import api from '../../api'

// import component
import { Table, Button, message, Modal, Input } from 'antd'
const { confirm } = Modal;

const ActiveTeacher = () => {
   const [users, setUsers] = useState([])
   const [filterword, setFilterWord] = useState("")

   useEffect(() => {
      getData()
   }, [])

   const getData = async () => {
      let users = await api.user.getUsers();
      // 
      users = users.filter(user => user.permission == "teacher" && user.InActive)
      setUsers(users)
   }

   const activeTeacher = async (id) => {
      confirm({
         title: '',
         content: '',
         async onOk() {
            let isSuccess = await api.user.activeUser(id)
            if (isSuccess) {
               console.log(isSuccess)
               message.success("")
               getData()
            } else {
               message.success("")
            }
         },
         onCancel() {
            console.log('Cancel');
         },
         okText: "",
         cancelText: ""
      });
   }
   const filter = (e) => {
      setFilterWord(e.target.value)
   }
   const columns = [
      {
         title: '',
         dataIndex: 'username',
         key: 'username',
      },
      {
         title: '',
         dataIndex: 'name',
         key: 'name',
      },
      {
         title: 'email',
         dataIndex: 'email',
         key: 'email',
      },
      {
         title: '',
         key: 'action',
         render: (text, record) => (
            <Button type="primary" onClick={() => activeTeacher(record._id)}></Button>
         ),
         align: 'right'
      },
   ];

   return (
      <div>
         <h3></h3>
         <h4></h4>
         <Input onChange={filter} placeholder=""></Input>
         <Table columns={columns} dataSource={users.filter(user => user.name.includes(filterword))} />
      </div>
   );
};

export default ActiveTeacher;